const moment = extendMoment(Moment)

import Validator from "fastest-validator"
const v = new Validator({
  messages: {
    enumValue: "Select '{field}' please.",
    string: "Check '{field}' please.",
    stringMin: "Check '{field}' please."
  }
});

import shortId from "shortid"

const stateDefinitions = {
  available: {
    color: 'white',
    label: 'Available',
  },
  // enquire: {
  //   color: '#ffd200',
  //   label: 'Enquire',
  // },
  unavailable: {
    selectable: false,
    color: '#78818b',
    label: 'Unavailable',
  },
};

const roomOptions = [
  {value: 'iowa', text: 'Room 1/Iowa '},
  {value: 'arizona', text: 'Room 2/Arizona '}
]
const titleOptions = [
  { value: 'mr', text:'Mr.' },
  { value: 'mrs', text:'Mrs.' },
  { value: 'ms',  text:'Ms.' }
];

const guestsNum = [
  { value: '1', text: '1 Guest' },
  { value: '2', text: '2 Guests.' },
  { value: '3', text: '3 Guests' },
  { value: '4', text: '4 Guests' },
  { value: '5', text: '5 Guests' }
];
const ChildrenNum = [
  { value: '0', text: '0 Child Included' },
  { value: '1', text: '1 Child Included' },
  { value: '2', text: '2 Children Included' },
  { value: '3', text: '3 Children Included' },

];

const ErrorMessage = styled(`div`)`
  color: ${colors.warning};
  font-family: ${options.systemFontFamily.join(`,`)};
  font-size: 0.875rem;
  margin: calc(1.05rem / 2) 0;
`

const SuccessMessage = styled(`div`)`
  color: ${colors.success};
  // font-family: ${options.systemFontFamily.join(`,`)};
  border: solid 1px black;
  text-align: center;
`
const schema = {
  //room/date/name/email is not blank, room meets date selection
  //use modal to confirm
  //validate fails, set the error message. passed then call on submit
    roomno: {type: "enum", values: ["iowa", "arizona"]},
    checkin: {type: "string", optional:true },
    checkout: {type: "string", optional:true },
    checkinArizona: {type: "string", optional:true },
    checkoutArizona: {type: "string", optional:true },
    firstname: {type: "string", min: 2},
    lastname: {type: "string", min: 2},
    email: {type: "email"},
    term : "boolean"
}

const roomMatchDates = ({roomno, checkin, checkout, checkinArizona, checkoutArizona}) => {
  // console.log(roomno, checkin, checkout, checkinArizona, checkoutArizona);
  // console.log(checkin&&checkout ? "good" : "bad")
  switch (roomno) {
    case 'iowa':
      return (checkin && checkout)? true : false;
      break;
    case 'arizona':
      return (checkinArizona && checkoutArizona)? true: false
      break;
    default:
      return false; 
  }
}

const yen = new Intl.NumberFormat('ja-JP', {style: 'currency', currency: 'JPY'});

class ReserveForm extends Component {
  constructor(){
    super();
    this.onSubmit = this.onSubmit.bind(this);
    this.validate = this.validate.bind(this);
    this.updatePredicate = this.updatePredicate.bind(this);
    this.calAmount = this.calAmount.bind(this);
  }
  state = {
    successMessage: ``,
    errorMessage: ``,
    fieldErrors: {},
    roomno: ``,
    checkin: ``,
    checkout: ``,
    checkinArizona: ``,
    checkoutArizona: ``,
    guests: `3`,
    children: `1`,
    title: `mrs`,
    firstname: ``,
    lastname: ``,
    email: ``,
    phone: ``,
    address1: ``,
    zipcode: ``,
    term: false,
    iowaRates: [],
    isDesktop: false,
    amount: ''
  }

  
  componentDidMount() {
    document.querySelectorAll('input').forEach(
      node => node.addEventListener('keypress', function (event) {
        if (event.keyCode === 13) {
          event.preventDefault();
        }
      })
    );
    this.updatePredicate();
    window.addEventListener("resize", this.updatePredicate);
    // fetch("http://localhost:3000/dates")
    fetch()
      .then(res => res.json())
      .then(
        (result) => {
          // console.log(result);
          this.setState({
            iowaDates: result.iowa.map(e => {
              return {
                state: e.state,
                range: moment.range(parseInt(e.checkin), parseInt(e.checkout)),
              }
            }),
            iowaRates: result.rates.filter(r => r.roomno === "iowa").map(
              a => {
                a.amount = a.rate;
                if(a.rate !== "Sold" ) a.rate = yen.format(parseInt(a.rate));
                return a;
              }
            ),
            arizonaDates: result.arizona.map(e => {
              return {
                state: e.state,
                range: moment.range(parseInt(e.checkin), parseInt(e.checkout))
              }
            }),
            arizonaRates: result.rates.filter(r => r.roomno === "arizona").map(
              a => {
                a.amount = a.rate;
                if(a.rate !== "Sold" ) a.rate = yen.format(parseInt(a.rate));
                return a;
              }
            ),
            isLoaded: true
          })
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      )
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.updatePredicate);
  }

  validate(e) {
    const vr = v.validate(this.state, schema);
    // console.log(vr);
    if(vr === true) {
      // console.log(roomMatchDates(this.state)? "g":"b");
      if(roomMatchDates(this.state)) {
        if (this.state.term) this.onSubmit(e);
        else this.setState({ errorMessage: "please check the term, then send" })
      } else this.setState({errorMessage: "The room chosen doesnt match checkin/out dates."})
    }
    else {
      const message = vr.map(e => <Message.Item>{e.message}</Message.Item>);
      this.setState({errorMessage: <Message error>{message}</Message>});
    } 
  }

  onSubmit(e) {
    e.preventDefault();
    const { roomno, checkin, checkout, checkinArizona, checkoutArizona, guests, children, title, firstname, lastname, email, phone, address1, zipcode} = this.state;
    // console.log(amount, "amount..............")
    // validation here or use Formik or something like that
    // we can also rely on server side validation like I do right now
    // console.log(this.reservation);
    let finalCheckin, finalCheckout, amount;
    if( roomno === "iowa")  { 
      finalCheckin = checkin;
      finalCheckout = checkout;
      amount = this.calAmount(finalCheckin, finalCheckout, this.state.iowaRates)
    } else if ( roomno === "arizona") { 
      finalCheckin = checkinArizona;
      finalCheckout = checkoutArizona;
      amount = this.calAmount(finalCheckin, finalCheckout, this.state.arizonaRates)
    } else {
      finalCheckin = "1970-01-01";
      finalCheckout = "1970-01-01";
    }

    // const { portalId, formId, sfdcCampaignId } = this.props
    const portalId = "5289196";
    const formId="e1c4a480-8784-46dc-b58d-3232c8a80383";
    const url = `https://api.hsforms.com/submissions/v3/integration/submit/${portalId}/${formId}`
    const data = {
      fields: [
        {
          name: `id`,
          value: shortId.generate()
        },
        {
          name: `roomno`,
          value: roomno
        },
        {
          name: `checkin`,
          value: finalCheckin
        },
        {
          name: `checkout`,
          value: finalCheckout
        },
        {
          name: `guests`,
          value: guests
        },
        {
          name: `children`,
          value: children
        },
        {
          name: `title`,
          value: title
        },
        {
          name: `firstname`,
          value: firstname
        },
        {
          name: `lastname`,
          value: lastname
        },
        {
          name: `email`,
          value: email
        },
        {
          name: `phone`,
          value: phone
        },
        {
          name: `address1`,
          value: address1
        },
        {
          name: `zip`,
          value: zipcode
        },
        {
          name: `amount`,
          value: amount
        }
      ],
      context: {
        pageUri: window.location.href,
        pageName: document.title,
      },
    }

    const xhr = new XMLHttpRequest()
    xhr.open(`POST`, url, false)
    xhr.setRequestHeader(`Content-Type`, `application/json`)
    xhr.setRequestHeader(`skipvalidation`,`true`)

    xhr.onreadystatechange = () => {
      if (xhr.readyState == 4) {
        const response = JSON.parse(xhr.responseText)
        if (xhr.status == 200) {
          this.setState({successMessage:  response.thankYouMessage || response.inlineMessage})
        } else {
          // console.log(response);
          let errorMessage,
            fieldErrors = {}
          if (response.errors) {
            const errorRe = /^Error in 'fields.([^']+)'. (.+)$/
            response.errors.map(error => {
              const [, fieldName, message] = errorRe.exec(error.message)
              fieldErrors[fieldName] = message
            })

            // hubspot use "The request is not valid" message, so probably better use custom one
            errorMessage = `invalid email address. If the email address is valid, please contact us by facebook or contact us by email directly`
          } else {
            errorMessage = response.message
          }

          this.setState({ fieldErrors, errorMessage })
        }
      }
    }
    xhr.send(JSON.stringify(data))
  }

  handleChange = (e, { name, value }) => {
    // console.log(e, name, value);
    if(moment.isRange(e)) {
      console.log(e.start.format('YYYY-MM-DD'), e.end.format('YYYY-MM-DD'));
      this.setState({ checkin: e.start.format('YYYY-MM-DD'), checkout: e.end.format('YYYY-MM-DD') });
    } else this.setState({ [name]: value });
    // console.log(this.state);
  }

  updatePredicate() {
    this.setState({ isDesktop: window.innerWidth > 700})
  }
  handleChangeArizona = (e, { name, value }) => {
    // console.log(e, name, value);
    if(moment.isRange(e)) {
      // console.log(e.start.format('YYYY-MM-DD'), e.end.format('YYYY-MM-DD'));
      this.setState({ checkinArizona: e.start.format('YYYY-MM-DD'), checkoutArizona: e.end.format('YYYY-MM-DD') });
    } else return;
  }

  handleClickPaste = (e) => {
    // console.log(e)
    e.preventDefault();
    // e.stopPropagation();
    navigator.clipboard.readText().then(text => this.setState({address1: text}))
    // console.log(text);
    // this.setState({address1: text})
  }
  
  calAmount = (cin, out, rates) => {
      // console.log(cin, out, rates);
      let m1 = moment(cin),
          m2 = moment(out),
          r = moment.range(m1, m2),
          ma = Array.from(r.by('days', { excludeEnd: true })),
          sa = ma.map(a => a.format('YYYY-MM-DD')),
          am = sa.reduce((acc, cur) => {
            let m = rates.find(r => r.date === cur),
                mm = m? m.amount : "Quote",
                c = parseInt(mm);
            return c + acc;
          }, 0),
          bm = am === 0 || isNaN(am) ? "Quote" : am;
          // this.setState({amount: bm}) reach deapest, cant setstate
          // console.log("amount.....", this.state.amount)
      // console.log("amount", am, sa, ma, r);
      return bm;
  }

  toggle = () => this.setState({term: !this.state.term})
  render() {
    const { roomno, checkin, checkout, checkinArizona, checkoutArizona, iowaRates, arizonaRates, dates, guests, children, title, firstname, lastname, email, phone, address1, zipcode } = this.state

    if(this.state.isLoaded){
      // console.log(this.state.iowaRates)
      return (
        <div>
          {this.state.successMessage ? (
            <Message success >
              <div dangerouslySetInnerHTML={{ __html: this.state.successMessage }} />
            </Message>
          ) : (
              <div>
                <Sticky offset={40} styleElement={{backgroundColor:'white', borderBottom: "solid lightgray 1px"}}>
                  <Divider horizontal>Reservation Summary</Divider>
                  <List>
                    <Popup trigger={
                      // <Dimmer.Dimmable  blurring={checkin ? false : true} >
                        <List.Item disabled={roomno === 'arizona' ? true : false}>
                          <List.Icon name='calendar alternate outline' />
                          <List.Content>IOWA: {checkin ? checkin : "checkin"} <Icon name="arrow right" />{checkout ? checkout : "checkout"}</List.Content>
                        </List.Item>
                      // </Dimmer.Dimmable>
                    }>
                      Select Dates from room Iowa's calendar below
                    </Popup>
                    <Popup trigger={
                      <List.Item disabled={roomno === "iowa" ? true : false}>
                        <List.Icon name='calendar alternate outline' />
                        <List.Content>ARIZONA: {checkinArizona ? checkinArizona : "checkin"}<Icon name="arrow right" />{checkoutArizona ? checkoutArizona : "checkout"}</List.Content>
                      </List.Item>
                    }>
                      Select Dates from room Arizona's calendar below
                    </Popup>
                    <List.Item>
                      <List.Icon name='hotel' />
                      <List.Content>Room Selected: {roomno? roomno.toUpperCase(): "Not yet"} / 
                      { roomno === 'iowa' ? yen.format(this.calAmount(checkin, checkout, iowaRates)) : 
                        roomno === 'arizona' ? yen.format(this.calAmount(checkinArizona, checkoutArizona, arizonaRates)) : "Not yet" }
                      </List.Content>
                    </List.Item>
                    {/* <List.Item>
                      <List.Icon name='yen' />
                      <List.Content>
                        {roomno === 'iowa' ? this.calAmount(checkin, checkout, iowaRates) : this.calAmount(checkinArizona, checkoutArizona, arizonaRates) }
                      </List.Content>
                    </List.Item> */}
                  </List>
                  {/* <Divider horizontal>~</Divider> */}
                </Sticky>
                <Divider horizontal>
                  <Header>
                    <Icon name='hotel' />
                    Room1/Iowa
                  </Header>
                </Divider>
                <DateRangePicker
                  css={{display: 'flex'}}
                  firstOfWeek={0}
                  numberOfCalendars={ this.state.isDesktop ? 2 : 1}
                  selectionType='range'
                  minimumDate={new Date()}
                  stateDefinitions={stateDefinitions}
                  dateStates={this.state.iowaDates}
                  defaultState="available"
                  // showLegend={true}
                  onSelect={this.handleChange}
                  rates={this.state.iowaRates}
                />
                {/* <Form>
                  <Form.Group widths='equal'>
                    <Form.Input
                      action={{ color: 'black', labelPosition: 'left', icon: 'calendar check', content: 'Iowa' }}
                      actionPosition='left'
                      size='mini'
                      placeholder='checkin'
                      name='checkin'
                      value={checkin ? checkin : ''}
                      type='text'
                      readOnly={true}
                      width='ten'
                    />
                    <Form.Input
                      action={{ color: 'black', labelPosition: 'left', icon: 'calendar times', content: 'Iowa' }}
                      actionPosition='left'
                      size='mini'
                      placeholder='checkout'
                      value={checkout ? checkout : ''}
                      type='text'
                      readOnly={true}
                      width='ten'
                    />
                  </Form.Group>
                </Form> */}
                <Divider horizontal> Or </Divider>

                <Divider horizontal>
                  <Header> 
                    {/* as='h3'> */}
                    <Icon name='hotel' />
                    Room2/Arizona
                  </Header>
                </Divider>
                <DateRangePicker
                  css={{display: 'flex'}} 
                  firstOfWeek={0}
                  numberOfCalendars={ this.state.isDesktop ? 2 : 1}
                  selectionType='range'
                  minimumDate={new Date()}
                  stateDefinitions={stateDefinitions}
                  dateStates={this.state.arizonaDates}
                  defaultState="available"
                  // showLegend={true}
                  onSelect={this.handleChangeArizona}
                  rates={this.state.arizonaRates}
                />
                {/* <Form>
                  <Form.Group widths='equal'>
                    <Form.Input
                      action={{ color: 'grey', labelPosition: 'left', icon: 'calendar check', content: 'Arizona' }}
                      actionPosition='left'
                      size='mini'
                      // icon=''
                      // iconPosition='left'
                      placeholder='checkin'
                      name='checkin'
                      value={checkinArizona ? checkinArizona : ''}
                      type='text'
                      readOnly={true}
                      width='ten'
                    />
                    <Form.Input
                      action={{ color: 'grey', labelPosition: 'left', icon: 'calendar times', content: 'Arizona' }}
                      actionPosition='left'
                      size='mini'
                      // icon='calendar times'
                      // iconPosition='left'
                      placeholder='checkout'
                      value={checkoutArizona ? checkoutArizona : ''}
                      type='text'
                      readOnly={true}
                      width='ten'
                    />
                  </Form.Group>
                </Form> */}
                <Divider horizontal>  ..... </Divider>

                <Form onSubmit={this.validate} >
                  <Form.Select required name='roomno' value={roomno} onChange={this.handleChange} label='Room?' options={roomOptions} placeholder='Room' />
                  <Form.Group widths='equal'>
                    <Form.Select required name='guests' value={guests} onChange={this.handleChange} label='Guests' options={guestsNum} placeholder='Guests' />
                    <Form.Select required name='children' value={children}  onChange={this.handleChange} label='Children Included' options={ChildrenNum} placeholder='Children Included' />
                  </Form.Group>
                  <Form.Group widths='equal'>
                    <Form.Select required name='title' value={title} onChange={this.handleChange} label='Title' options={titleOptions} placeholder='Title' />
                    <Form.Input  required name='firstname' value={firstname} onChange={this.handleChange} fluid label='First name' placeholder='First name' />
                    <Form.Input  required name='lastname' value={lastname} onChange={this.handleChange} fluid label='Last name' placeholder='Last name' />
                  </Form.Group>
                  <Form.Group widths='equal'>
                    <Form.Input required name='email' value={email} onChange={this.handleChange} fluid label='Email' placeholder='Email address' />
                    <Form.Input name='phone' value={phone} onChange={this.handleChange} type='text' fluid label='Phone' placeholder='phone number' />
                  </Form.Group>
                  <Form.Group widths='equal'>
                    <Form.Input action={{icon:'paste', onClick: this.handleClickPaste}} required name='address1' value={address1} onChange={this.handleChange} fluid label='Address' placeholder='Address' />
                    <Form.Input name='zipcode' value={zipcode} onChange={this.handleChange} fluid label='zipcode' placeholder='zipcode' />
                  </Form.Group>
                  {/* <Form.TextArea label='About' placeholder='Tell us more about ...' /> */}
                  <Form.Checkbox required onChange={this.toggle} label='I agree to the Terms of Booking.' />
                  <Form.Button fluid tabIndex='1'>Contact</Form.Button>
                </Form>
                {/* {this.state.fieldErrors.reservation && (
                  <ErrorMessage>{this.state.fieldErrors.reservation}</ErrorMessage>
                )} */}
                {this.state.errorMessage && (
                  <ErrorMessage>{this.state.errorMessage}</ErrorMessage>
                )}
              </div>
            )
          }
        </div>
      )
    } else {
      return <div> Loading... </div>;
    }
  }
}

export default ReserveForm 
